body {
  font-family: "Noto Sans KR", sans-serif;
  display: grid;
  grid-template-columns: 1fr 1160px 1fr;
  white-space: nowrap;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Gowun Dodum", sans-serif;
}

ul,
ol {
  list-style: none;
  padding-left: 0px;
}

a {
  text-decoration: none;
  color: black;
}

.container {
  display: grid;
  grid-template-rows: 200px 1fr 150px;
  grid-template-columns: 250px 1fr;
}

header {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  background-color: none;

  display: flex;
  flex-direction: column;
  justify-content: center;

  border-bottom: solid 2px;
  border-color: #9e9e9e;
}

header h1 {
  margin-left: 10%;
}

aside {
  padding: 20px 10px 10px 20px;
  background-color: none;
}

aside .project_ol {
  border-left: solid 2px;
  border-color: #9e9e9e;

  padding-left: 20px;
}

aside .project_ol h4 {
  margin-top: 10px;
  margin-bottom: 10px;
}

main {
  margin-top: 40px;
  margin-bottom: 40px;
  border-left: solid 2px;
  border-color: #9e9e9e;
  padding: 0px 110px 0px 110px;
  background-color: none;
}

footer {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  background-color: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  border-top: solid 2px;
  border-color: #9e9e9e;
}

.footer-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-icon i {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 10px;
}

.profile {
  margin-top: 40px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#profile_contents_detail {
  margin-top: 40px;
}

#profile_contents_detail li {
  margin-bottom: 10px;
}

.main_itemBox {
  border: solid 2px;
  /* border-color: #9e9e9e; */
  border-color: white;
  height: 50px;
  background-color: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.main_review {
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

.main_review i {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 30%;
}

.main_review .write_info {
  display: flex;
  justify-content: right;
}

.main_review .info_writer {
  font-weight: 600;
}

.main_review .info_date {
  margin-top: 3px;
  color: #9e9e9e;
  font-size: smaller;
}

.main_review .review_main {
  white-space: normal;
}

.profile_image {
  width: 350px;
  height: 320px;
  border-radius: 5%;
  overflow: hidden;
}

.profile_image img {
  width: 100%;
  height: 100%;
}

.graph {
  margin-top: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.graph img {
  width: 660px;
  height: 100px;
}

.about-content {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.about-content li {
  margin-bottom: 10px;
}

.Archiving_flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Archiving_flex img {
  width: 320px;
  height: 230px;
  border-radius: 5%;
  overflow: hidden;

  border: solid 1px;
  border-color: #9e9e9e;
}

.Archiving_flex ul {
  margin-top: 30px;
  margin-bottom: 30px;
  overflow: hidden;
}

.Archiving_flex li {
  margin-bottom: 10px;
  overflow: hidden;
}

.ProjectList_flexBox {
  padding-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Project_itemBox {
  border: solid 2px;
  border-color: #9e9e9e;

  width: 320px;
  height: 280px;
  background-color: none;
}

.Project_itemBox img {
  padding-left: 30px;
  padding-top: 30px;
  width: 80%;
  height: 60%;
}

.Project_itemBox h3 {
  padding-left: 30px;
}

.project_img img {
  border: solid 2px;
  border-color: #9e9e9e;
  width: 690px;
}

.list_box {
  padding-top: 10px;
  padding-bottom: 20px;
}

.listflex_box {
  padding-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: left;
}

.listflex_box .label {
  width: 150px;
}

.text {
  white-space: normal;
}

.review_box {
  margin-top: 50px;
}
.main_detail {
  margin-bottom: 30px;
}
.review_box .review_info {
  display: flex;
  justify-content: right;
  margin-bottom: 5px;
}

.review_box .review_info input {
  border: solid 1px black;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  margin-left: 3px;
}

#main_box {
  width: 100%;
  height: 30px;
  padding: 5px;
  box-sizing: border-box;
  border: solid 1px black;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 14px;
  outline: none;
  resize: none;
}

.review_box textarea {
  width: 100%;
  height: 100px;
  padding: 5px;
  box-sizing: border-box;
  border: solid 1px black;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 16px;
  outline: none;
  resize: none;
}

.review_box .submit_box {
  margin-top: 10px;
  display: flex;
  justify-content: right;
}

.review_box .submit_box input {
  width: 65px;
  height: 30px;
  border: none;
}

.review .write_info {
  margin-top: 20px;
  display: flex;
  justify-content: left;
}

.review .info_writer {
  margin-right: 10px;
  font-weight: 600;
}

.review .info_date {
  margin-top: 3px;
  color: #9e9e9e;
  font-size: smaller;
}

.review .review_main {
  margin-top: 10px;
  white-space: normal;
}
@media (max-width: 1100px) {
  body {
    display: flow-root;
    grid-template-columns: 1fr 1160px 1fr;
  }

  .container {
    display: flow-root;
  }

  header {
    height: 200px;
  }

  aside {
    border-bottom: solid 2px;
    border-color: #9e9e9e;
  }

  footer {
    height: 150px;
  }

  main {
    padding: 10px 30px 10px 30px;
    border-left: none;
    border-color: none;
  }

  .graph {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .graph img {
    width: 80%;
    height: 80%;
  }

  .graph_flex {
    padding-left: 80px;
  }

  .Archiving_flex {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .ProjectList_flexBox {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .text {
    width: 690px;
  }
}

@media (max-width: 770px) {
  .profile {
    display: flex;
    flex-direction: column;
  }

  .about-content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .graph img {
    width: 100%;
    height: 100%;
  }

  .graph_flex {
    padding-left: 0px;
  }

  .Archiving_flex {
    display: flow-root;
    flex-direction: row;
    justify-content: space-around;
  }

  .ProjectList_flexBox {
    padding-bottom: 0px;
    display: flow-root;
    flex-direction: row;
    justify-content: space-around;
  }

  .Project_itemBox {
    margin-bottom: 30px;
  }

  .project_img img {
    width: 100%;
  }

  .listflex_box {
    padding-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: left;
    white-space: normal;
  }

  .text {
    width: 100%;
  }
}
